
import { defineComponent, ref, Ref, onMounted, isProxy } from 'vue';
import { useRoute, useRouter } from 'vue-router';
import { ILogin } from '@/types/Interfaces';

import {formRequired, emailValidator, validate} from '@/mixins/FormValidator';

import useAuth from '@/types/Auth';
import useCs from '@/types/Cs';

export default defineComponent({
  components: {
  },
  setup() {
    const router = useRouter();
    
    const {
      createToken,
      storeToken,
      removeToken,
      getUserId,
      getProfile,
      storeProfile,
      removeProfile,
      getProfileFromLS
    } = useAuth();
    
    const {
      getCs
    } = useCs();

    const formData = ref<ILogin>({
      email: '',
      password: '' 
    });
    
    const errors: Ref<{ [key: string]: string }> = ref({
      email: '',
      password: ''  
    });

    const validators: { [key: string]: Function[] } = {
      email: [
        formRequired,
        emailValidator
      ],
      password: [
        formRequired
      ]
    }
    
    
    const form = ref<HTMLFormElement | null>(null);

    const loginError = ref<string>('');
    
    const setLoginError = (message: string) => {
      loginError.value = message;
      window.setTimeout(() => {
        loginError.value = '';
      }, 5000);  
    };
    
    onMounted(() => {
      removeToken()
    });

    const loading = ref(false);

    const onLogin = async (event: Event) => {
      // validate
      // if (form.value) {
      //   const isValid = form.value.checkValidity();
      //   if (!isValid) {
      //     alert('not vaida')
      //     return;
      //   }
      // }
      if (!validate(formData.value, validators, errors)) return;
      
      try {
        loading.value = true;
        const token = await createToken(formData.value);
        storeToken(token);
        // const userId = await getUserId();
        const profile = token.user // await getCs(userId)
        storeProfile(profile)
        router.push({ name: 'Dashboard' }) ;
      } catch (err) {
        console.log(err)
        if (err.response) {
          const { status, data } = err.response;
          console.log(data)
          if (status >= 400 && status < 500) {
            setLoginError('ログインに失敗しました。')
          } else {
            setLoginError(JSON.stringify(data))
          }
          // formData.value = {
          //   email: '',
          //   password: ''
          // }
        } else {
          // network error or timeout
          setLoginError('ネットワークエラー')
        }
        
      }
      loading.value = false;
      
    };
    const isPasswordHidden = ref(true);
    return {
      loading,
      form,
      formRequired,
      formData,
      loginError,
      // formValid,
      onLogin,
      isPasswordHidden,
      errors, 
    };
  }
})
